interface CoverTileProps {
  tileId: string;
  content: CoverTileContent;
  design: CoverTileDesign;
  viewportSettings: ViewportSettings;
}

function computedLayoutClass(layout: CoverTileLayout | undefined): string {
  switch (layout) {
    case 'FULLSCREEN_CENTER':
      return 'fullscreen-center';
    case 'FULLSCREEN_LEFT':
      return 'fullscreen-left';
    case 'FULLSCREEN_CENTER_LEFT':
      return 'fullscreen-center-left';
    case 'FULLSCREEN_RIGHT':
      return 'fullscreen-right';
    case 'FULLSCREEN_BOTTOM':
      return 'fullscreen-bottom';
    case 'FULLSCREEN_BOTTOM_RIGHT':
      return 'fullscreen-bottom-right';
    case 'FULLSCREEN_BOTTOM_LEFT':
      return 'fullscreen-bottom-left';
    case 'FULLSCREEN_TOP':
      return 'fullscreen-top';
    case 'SIDEBAR_RIGHT':
      return 'sidebar-right';
    case 'SIDEBAR_LEFT':
      return 'sidebar-left';
    case 'HALFSCREEN_LEFT':
      return 'halfscreen-left';
    case 'HALFSCREEN_CENTER':
      return 'halfscreen-center';
    case 'COLLAGE_BOTTOM':
      return 'collage-bottom';
    case 'COLLAGE_LEFT':
      return 'collage-left';
    default:
      return 'fullscreen-center';
  }
}

function computedScrollEffectClass(
  layout: CoverTileLayout | undefined,
  isIOSDevice: boolean,
  scrollEffect?: ScrollEffect,
): string | undefined {
  if (layout === 'COLLAGE_BOTTOM' || layout === 'COLLAGE_LEFT') {
    return undefined;
  }

  switch (scrollEffect) {
    case ScrollEffect.NONE:
      return undefined;
    case ScrollEffect.PARALLAX:
      return 'ins-tile__background--parallax';
    case ScrollEffect.FIXED:
      if (isIOSDevice) {
        return undefined;
      }
      return 'ins-tile__background--fixed';
    case ScrollEffect.ZOOM_OUT:
      return 'ins-tile__background--scale';
    default:
      return undefined;
  }
}

// Для указанных лейаутов особый лимит из-за его размеров в css
function getMinLimit(
  layout: CoverTileLayout | undefined,
  tileOffsetHeight: number,
  speed: number,
): number {
  const result = tileOffsetHeight / speed;
  switch (layout) {
    case 'HALFSCREEN_LEFT':
    case 'HALFSCREEN_CENTER':
      return result * 2;
    default:
      return result;
  }
}

function getParallaxTransform(
  layout: CoverTileLayout | undefined,
  windowScrollTop?: number,
  tileOffsetTop?: number,
  tileOffsetHeight?: number,
): Record<string, unknown> {
  const speed = 4; // Костанта дублируется в scss
  const currentWindowScrollTop = windowScrollTop ?? 0;
  const currentOffsetTop = tileOffsetTop ?? 0;
  const currentOffsetHeight = tileOffsetHeight ?? 0;
  const maxLimit = currentOffsetHeight / speed;
  const minLimit = -getMinLimit(layout, currentOffsetHeight, speed);
  const subresult = (currentWindowScrollTop - currentOffsetTop) / speed;
  const result = Math.min(maxLimit, Math.max(minLimit, subresult));

  return {
    transform: `translateY(${result}px)`,
  };
}

function getScaleTransform(
  windowScrollTop?: number,
  tileOffsetTop?: number,
  tileOffsetHeight?: number,
): Record<string, unknown> {
  const currentScrollTop = windowScrollTop ?? 0;
  const currentOffsetTop = tileOffsetTop ?? 0;
  const currentOffsetHeight = tileOffsetHeight ?? 0;
  const factor = currentOffsetHeight / 0.12; // Костанта дублируется в scss
  const maxLimit = 1.3;
  const minLimit = 1;
  const subresult = 1 - (currentScrollTop - currentOffsetTop - currentOffsetHeight) / factor;
  const result = Math.min(maxLimit, Math.max(minLimit, subresult));

  return {
    transform: `scale(${result})`,
  };
}

function computedScrollEffectStyle(
  layout: CoverTileLayout | undefined,
  scrollEffect?: ScrollEffect,
  windowScrollTop?: number,
  tileOffsetTop?: number,
  tileOffsetHeight?: number,
): Record<string, unknown> | undefined {
  if (typeof document === 'undefined') {
    return undefined;
  }

  if (layout === 'COLLAGE_BOTTOM' || layout === 'COLLAGE_LEFT') {
    return undefined;
  }

  switch (scrollEffect) {
    case ScrollEffect.PARALLAX:
      return getParallaxTransform(layout, windowScrollTop, tileOffsetTop, tileOffsetHeight);
    case ScrollEffect.ZOOM_OUT:
      return getScaleTransform(windowScrollTop, tileOffsetTop, tileOffsetHeight);
    default:
      return undefined;
  }
}

function getDefaults(layout: CoverTileLayout | undefined): CoverTileLayoutDefaults | undefined {
  switch (layout) {
    case 'FULLSCREEN_CENTER':
      return FullscreenCenterDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_LEFT':
      return FullscreenLeftDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_CENTER_LEFT':
      return FullscreenCenterLeftDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_RIGHT':
      return FullscreenRightDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_BOTTOM':
      return FullscreenBottomDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_BOTTOM_RIGHT':
      return FullscreenBottomRightDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_BOTTOM_LEFT':
      return FullscreenBottomLeftDefaults as CoverTileLayoutDefaults;
    case 'FULLSCREEN_TOP':
      return FullscreenTopDefaults as CoverTileLayoutDefaults;
    case 'SIDEBAR_RIGHT':
      return SidebarRightDefaults as CoverTileLayoutDefaults;
    case 'SIDEBAR_LEFT':
      return SidebarLeftDefaults as CoverTileLayoutDefaults;
    case 'HALFSCREEN_LEFT':
      return HalfscreenLeftDefaults as CoverTileLayoutDefaults;
    case 'HALFSCREEN_CENTER':
      return HalfscreenCenterDefaults as CoverTileLayoutDefaults;
    case 'COLLAGE_BOTTOM':
      return CollageBottomDefaults as CoverTileLayoutDefaults;
    case 'COLLAGE_LEFT':
      return CollageLeftDefaults as CoverTileLayoutDefaults;
    default:
      return undefined;
  }
}

function getDesignSettings(
  userDesign: CoverTileDesign,
  defaults?: CoverTileLayoutDefaults,
): CoverTileDesign {
  const general: GeneralDesignSettings = {
    ...defaults?.general,
    ...userDesign.general,
    background: {
      ...defaults?.general?.background,
      ...userDesign.general?.background,
      solid: {
        ...defaults?.general?.background?.solid,
        ...userDesign.general?.background?.solid,
      },
      gradient: {
        ...defaults?.general?.background?.gradient,
        ...userDesign.general?.background?.gradient,
      },
    },
  };
  const tagline: TextSettings = {
    ...(defaults?.tagline as TextSettings),
    ...userDesign.tagline,
  };
  const headline: TextSettings = {
    ...(defaults?.headline as TextSettings),
    ...userDesign.headline,
  };
  const description: TextSettings = {
    ...(defaults?.description as TextSettings),
    ...userDesign.description,
  };
  const primaryActionLink: ActionLinkSettings = {
    ...(defaults?.primaryActionLink as ActionLinkSettings),
    ...userDesign.primaryActionLink,
  };
  const secondaryActionLink: ActionLinkSettings = {
    ...(defaults?.secondaryActionLink as ActionLinkSettings),
    ...userDesign.secondaryActionLink,
  };
  const image: ScrollableImageSettings = {
    ...defaults?.image,
    ...userDesign.image,
    overlay: {
      ...defaults?.image?.overlay,
      ...userDesign.image?.overlay,
      solid: {
        ...defaults?.image?.overlay?.solid,
        ...userDesign.image?.overlay?.solid,
      },
      gradient: {
        ...defaults?.image?.overlay?.gradient,
        ...userDesign.image?.overlay?.gradient,
      },
    },
  };
  const arrow: ArrowSettings = {
    ...(defaults?.arrow as ArrowSettings),
    ...userDesign.arrow,
  };
  const sidebar: SidebarSettings = {
    ...(defaults?.sidebar as SidebarSettings),
    ...userDesign.sidebar,
  };

  return {
    layout: userDesign.layout,
    general,
    tagline,
    headline,
    description,
    primaryActionLink,
    secondaryActionLink,
    image,
    arrow,
    sidebar,
  };
}

function arrowStyles(arrowSettings?: ArrowSettings): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  if (arrowSettings?.color !== undefined) {
    styles = [
      ...styles,
      ...colorStyles('--arrow-color', arrowSettings.color),
    ];
  }

  return styles;
}

function customStyles(sidebar?: SidebarSettings): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  if (sidebar?.color !== undefined) {
    styles = [
      ...styles,
      ...colorStyles('--sidebar-solid-color', sidebar.color),
    ];
  }

  return styles;
}

function tileStyles(
  design: CoverTileDesign,
  layoutHasArrow: boolean,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('tagline', design.tagline),
    ...textStyles('headline', design.headline),
    ...textStyles('description', design.description),
    ...actionLinkStyles('primary-button', design.primaryActionLink),
    ...actionLinkStyles('secondary-button', design.secondaryActionLink),
    ...imageOverlayStyles('image', design.image),
    ...customStyles(design.sidebar),
  ];

  if (layoutHasArrow) {
    styles = [
      ...styles,
      ...arrowStyles(design.arrow),
    ];
  }

  return styles;
}

function getBackgroundPositionStyle(
  bgContainerWidth?: number,
  bgContainerHeight?: number,
  bgImageWidth?: number,
  bgImageHeight?: number,
): StyleKeyValue[] {
  const currentBgContainerWidth = bgContainerWidth ?? 0;
  const currentBgContainerHeight = bgContainerHeight ?? 0;
  const currentBgImageWidth = bgImageWidth ?? 0;
  const currentBgImageHeight = bgImageHeight ?? 0;

  if (!currentBgContainerWidth || !currentBgImageWidth) {
    return [];
  }

  const scaleX = currentBgImageWidth / currentBgContainerWidth;
  const scaleY = currentBgImageHeight / currentBgContainerHeight;
  let width = currentBgContainerWidth;
  let height = currentBgContainerHeight;
  let positionX = 0;
  let positionY = 0;

  if (scaleX < scaleY) {
    height = currentBgImageHeight / scaleX;
    positionY = (currentBgContainerHeight - height) / 2;
  } else {
    width = currentBgImageWidth / scaleY;
    positionX = (currentBgContainerWidth - width) / 2;
  }
  // Делаем поправку на неточные вычисления чтобы избежать образования белых полос по краям
  return [
    { key: '--background-size-x', value: `${width + 1}px` },
    { key: '--background-size-y', value: `${height + 1}px` },
    { key: '--background-position-x', value: `${positionX - 0.5}px` },
    { key: '--background-position-y', value: `${positionY - 0.5}px` },
  ];
}

function backgroundPositionStyles(
  isIOSDevice: boolean,
  image?: ImageData,
  scrollEffect?: ScrollEffect,
  bgContainerWidth?: number,
  bgContainerHeight?: number,
): StyleKeyValue[] {
  if (scrollEffect === ScrollEffect.FIXED && !isIOSDevice) {
    return getBackgroundPositionStyle(
      bgContainerWidth,
      bgContainerHeight,
      image?.set?.['default-1200x1200']?.width,
      image?.set?.['default-1200x1200']?.height,
    );
  }
  return [];
}

function useBackgroundOffsetSettings(
  bgContainerRef: Ref<HTMLElement | undefined>,
): {
  bgContainerWidth: Ref<number>;
  bgContainerHeight: Ref<number>;
  subscribeOnBackgroundOffsetChange: () => void;
  } {
  const bgContainerWidth = ref(0);
  const bgContainerHeight = ref(0);

  function updateOffset(): void {
    if (bgContainerRef.value === undefined || bgContainerRef.value === null) {
      return;
    }

    bgContainerWidth.value = bgContainerRef.value.offsetWidth;
    bgContainerHeight.value = bgContainerRef.value.offsetHeight;
  }

  function subscribeOnBackgroundOffsetChange(): void {
    const debounceUpdateOffset = debounce(updateOffset, 120); // позже, чем updateViewportDimensions
    window.addEventListener('resize', debounceUpdateOffset);
    updateOffset();
  }

  return {
    bgContainerWidth,
    bgContainerHeight,
    subscribeOnBackgroundOffsetChange,
  };
}

function usePerformArrowAction(tileRef: Ref<HTMLElement | undefined>): () => void {
  function performAction(): void {
    if (tileRef.value === undefined) {
      return;
    }

    const currentOffsetTop = tileRef.value.offsetTop;
    const currentOffsetHeight = tileRef.value.offsetHeight;

    window.scrollTo({
      top: currentOffsetTop + currentOffsetHeight,
      behavior: 'smooth',
    });
  }

  return performAction;
}

function layoutWithInlineImage(layout: CoverTileLayout | undefined): boolean {
  switch (layout) {
    case 'COLLAGE_BOTTOM':
      return true;
    default:
      return false;
  }
}

function layoutWithArrow(layout: CoverTileLayout | undefined): boolean {
  switch (layout) {
    case 'FULLSCREEN_CENTER':
    case 'FULLSCREEN_CENTER_LEFT':
    case 'FULLSCREEN_RIGHT':
      return true;
    default:
      return false;
  }
}

const CoverTile = defineComponent({
  components: {
    ArrowDownIconLarge,
    FontLoader,
    ActionLink,
    TileTitle,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    content: {
      required: true,
      type: Object as PropType<CoverTileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<CoverTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: CoverTileProps, { attrs, root, emit }) {
    const tileRef = ref<HTMLElement>();
    const bgContainerRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const {
      bgContainerWidth,
      bgContainerHeight,
      subscribeOnBackgroundOffsetChange,
    } = useBackgroundOffsetSettings(bgContainerRef);

    const browser = detect();
    const isIOSDevice = browser?.os === 'iOS'
      || (browser?.os === 'Mac OS' && 'ontouchstart' in window);

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(props.design.layout)}`);
    const design = computed(() => {
      const defaults = getDefaults(props.design.layout);
      return getDesignSettings(props.design, defaults);
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.tagline?.font,
      design.value.headline?.font,
      design.value.description?.font,
      design.value.primaryActionLink?.font,
      design.value.secondaryActionLink?.font,
    ]);

    const layoutHasInlineImage = computed(() => layoutWithInlineImage(props.design.layout));
    const layoutHasArrow = computed(() => layoutWithArrow(props.design.layout));

    const styleSelector = computed(() => {
      let styles: StyleKeyValue[] = [];
      styles = [
        ...tileStyles(
          design.value,
          layoutHasArrow.value,
        ),
        ...backgroundPositionStyles(
          isIOSDevice,
          props.content.image,
          design.value.image?.scrollEffect,
          bgContainerWidth.value,
          bgContainerHeight.value,
        ),
      ];

      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const hasTagline = computed(() => {
      const isTaglineVisible = design.value.tagline?.visible;
      const hasTaglineContent = isNotNullOrEmpty(props.content.tagline);
      return isTaglineVisible && hasTaglineContent;
    });
    const hasHeadline = computed(() => {
      const isHeadlineVisible = design.value.headline?.visible;
      const hasHeadlineContent = isNotNullOrEmpty(props.content.headline);
      return isHeadlineVisible && hasHeadlineContent;
    });
    const hasDescription = computed(() => {
      const isDescriptionVisible = design.value.description?.visible;
      const hasDescriptionContent = isNotNullOrEmpty(props.content.description);
      return isDescriptionVisible && hasDescriptionContent;
    });
    const hasImage = computed(() => {
      const isImageVisible = design.value.image?.visible;
      const hasImageContent = props.content.image !== undefined;
      return isImageVisible && hasImageContent;
    });
    const hasArrow = computed(() => {
      const isArrowVisible = design.value.arrow?.visible;
      return isArrowVisible && layoutHasArrow.value;
    });

    const performArrowAction = usePerformArrowAction(tileRef);
    const primaryActionLinkContent = computed(() => props.content.primaryActionLink);
    const primaryActionLinkDesign = computed(() => design.value.primaryActionLink);
    const secondaryActionLinkContent = computed(() => props.content.secondaryActionLink);
    const secondaryActionLinkDesign = computed(() => design.value.secondaryActionLink);
    const isPreview = root.$store.getters.isPreviewMode;
    const hasAnyActionLink = computed(() => {
      const hasPrimaryActionLinkElement = hasActionLinkElement(
        primaryActionLinkContent,
        primaryActionLinkDesign,
      );
      const hasSecondaryActionLinkElement = hasActionLinkElement(
        secondaryActionLinkContent,
        secondaryActionLinkDesign,
      );
      return hasPrimaryActionLinkElement || hasSecondaryActionLinkElement;
    });

    const scrollEffectClass = computed(() => computedScrollEffectClass(
      props.design.layout,
      isIOSDevice,
      design.value.image?.scrollEffect,
    ));

    const scrollEffectStyle = computed(() => computedScrollEffectStyle(
      props.design.layout,
      design.value.image?.scrollEffect,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const uploadingImage = computed(() => {
      const { imageListInUploadingState } = root.$store.state as State;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'image');
    });
    const isUploadingImage = computed(() => uploadingImage.value !== undefined);

    const imageStyleSelector = computed(() => {
      const imageSet: ImageSet | undefined = getImageSet(
        root.$store.state.context,
        uploadingImage.value,
        props.content.image,
      );

      const styles: string[] = [];
      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: undefined,
          image1x: imageSet?.urlCropped1000,
          image2x: imageSet?.urlCropped1000,
          backgroundColor: imageSet?.backgroundColor,
        },
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_S,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_L,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url3200,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_XL,
      ));

      /*
       Исключение. Тут в коде задается размер фона (баги ios+fixed) исходя из пропорций оригинала,
       поэтому в режиме fixed используем стандартную картинку
       */
      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__background--fixed .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url1200,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.TO_S,
      ));

      return styles.join('');
    });

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
      subscribeOnBackgroundOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      tileRef,
      bgContainerRef,
      layoutClass,
      fonts,
      styleSelector,
      imageStyleSelector,
      hasTagline,
      hasHeadline,
      hasDescription,
      hasImage,
      layoutHasInlineImage,
      hasAnyActionLink,
      hasArrow,
      performArrowAction,
      primaryActionLinkContent,
      primaryActionLinkDesign,
      secondaryActionLinkContent,
      secondaryActionLinkDesign,
      isPreview,
      scrollEffectClass,
      scrollEffectStyle,
      isUploadingImage,
      isTileShown,
    };
  },
});

export default CoverTile;
