import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { ActionLinkData, ActionLinkType } from '@/app/shared/client/type/action-link';
import {
  ActionLinkAppearance,
  ActionLinkSettings,
  ActionLinkSize,
  ButtonStyle,
} from '@/app/shared/client/type/settings/action-link-settings';
import { useScrollPosition } from '@/app/website/client/logic/scroll-position';
import { getLinkTarget } from '@/app/website/client/helper';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';

interface ActionLinkElement {
  readonly title: Ref<string | undefined>;
  readonly link: Ref<string | undefined>;
  readonly linkTarget: Ref<string>;
  readonly hasElement: Ref<boolean>;
  readonly isButton: Ref<boolean>;
  readonly isText: Ref<boolean>;
  readonly isSmall: Ref<boolean>;
  readonly isMedium: Ref<boolean>;
  readonly isLarge: Ref<boolean>;
  readonly isOutline: Ref<boolean>;
  readonly isSolid: Ref<boolean>;
  readonly isRect: Ref<boolean>;
  readonly isPill: Ref<boolean>;
  readonly isRound: Ref<boolean>;
  readonly performAction: () => void;
  readonly href: Ref<string | undefined>;
}

function getHref(content: Ref<ActionLinkData | undefined>): string | undefined {
  const actionLinkData = content.value;
  if (actionLinkData === undefined) {
    return undefined;
  }

  switch (actionLinkData.type) {
    case ActionLinkType.HYPER_LINK: return actionLinkData.link;
    case ActionLinkType.MAIL_LINK: return `mailto:${actionLinkData.email}`;
    case ActionLinkType.TEL_LINK: return `tel:${actionLinkData.phone}`;
    case ActionLinkType.SCROLL_TO_TILE: return undefined;
    default: throw new Error('Unknown ActionLinkType: ');
  }
}

function useActionPerformHandler(
  store: Store<State>,
  content: Ref<ActionLinkData | undefined>,
  linkTarget: Ref<string>,
): () => void {
  function performAction(): void {
    const actionLinkData = content.value;
    if (actionLinkData == null) {
      return;
    }
    switch (actionLinkData.type) {
      case ActionLinkType.HYPER_LINK:
        if (isNotNullOrEmpty(actionLinkData.link)) {
          window.open(actionLinkData.link, linkTarget.value);
        }
        break;
      case ActionLinkType.MAIL_LINK:
        if (isNotNullOrEmpty(actionLinkData.email)) {
          window.open(`mailto:${actionLinkData.email}`, '_self');
        }
        break;
      case ActionLinkType.TEL_LINK:
        if (isNotNullOrEmpty(actionLinkData.phone)) {
          window.open(`tel:${actionLinkData.phone}`, '_self');
        }
        break;
      case ActionLinkType.SCROLL_TO_TILE: {
        const scrollPosition = useScrollPosition(store);
        scrollPosition.scrollToTile(actionLinkData.tileId, true);
        break;
      }
      default:
        throw new Error('Unknown ActionLinkType: ');
    }
  }

  return performAction;
}

export function hasActionLinkElement(
  content: Ref<ActionLinkData | undefined>,
  design: Ref<ActionLinkSettings | undefined>,
): boolean {
  const isVisible = design.value?.visible ?? false;
  const hasTitle = isNotNullOrEmpty(content.value?.title);
  return isVisible && hasTitle;
}

export function useActionLinkElement(
  store: Store<State>,
  content: Ref<ActionLinkData | undefined>,
  design: Ref<ActionLinkSettings | undefined>,
): UnwrapRef<ActionLinkElement> {
  const isPreviewMode = computed(() => store.getters.isPreviewMode);

  const title = computed(() => content.value?.title);
  const link = computed(() => content.value?.link);
  const linkTarget = computed(() => getLinkTarget(isPreviewMode.value));

  const performAction = useActionPerformHandler(store, content, linkTarget);
  const href = computed(() => getHref(content));

  const hasElement = computed(() => hasActionLinkElement(content, design));

  const appearance = computed(() => design.value?.appearance);
  const size = computed(() => design.value?.size);
  const style = computed(() => design.value?.style);

  const isButton = computed(
    () => appearance.value === ActionLinkAppearance.OUTLINE_BUTTON
      || appearance.value === ActionLinkAppearance.SOLID_BUTTON,
  );
  const isText = computed(
    () => appearance.value === ActionLinkAppearance.TEXT_LINK,
  );

  const isSmall = computed(
    () => size.value === ActionLinkSize.SMALL,
  );
  const isMedium = computed(
    () => size.value === ActionLinkSize.MEDIUM,
  );
  const isLarge = computed(
    () => size.value === ActionLinkSize.LARGE,
  );

  const isOutline = computed(
    () => appearance.value === ActionLinkAppearance.OUTLINE_BUTTON,
  );
  const isSolid = computed(
    () => appearance.value === ActionLinkAppearance.SOLID_BUTTON,
  );

  const isRect = computed(
    () => style.value === ButtonStyle.RECTANGLE,
  );
  const isPill = computed(
    () => style.value === ButtonStyle.PILL,
  );
  const isRound = computed(
    () => style.value === ButtonStyle.ROUND_CORNER,
  );

  return reactive({
    title,
    link,
    linkTarget,
    hasElement,
    isButton,
    isText,
    isSmall,
    isMedium,
    isLarge,
    isOutline,
    isSolid,
    isRect,
    isPill,
    isRound,
    performAction,
    href,
  });
}

export default {};
