import { ImageInUploadingState } from '@/app/shared/client/type/image-in-uploading-state';

export function getImageInUploadingState(
  imageListInUploadingState: ImageInUploadingState[],
  tileId: string,
  imageId: string,
): ImageInUploadingState | undefined {
  return imageListInUploadingState
    .find((imageInUploadingState) => {
      const tileIdMatch = imageInUploadingState.tileId === tileId;
      const imageIdMatch = imageInUploadingState.imageId === imageId;
      return tileIdMatch && imageIdMatch;
    });
}

export function isImageInUploadingState(
  imageListInUploadingState: ImageInUploadingState[],
  tileId: string,
  imageId: string,
): boolean {
  const foundImageInUploadingState = getImageInUploadingState(
    imageListInUploadingState,
    tileId,
    imageId,
  );

  return foundImageInUploadingState !== undefined;
}
