import { Bucket } from '@/app/shared/client/type/bucket';

export default function getImageUrl(
  suffix: string | undefined,
  bucket: Bucket | undefined,
  bucketUrls: Record<Bucket, string | undefined>,
): string | undefined {
  if (suffix === undefined || bucket === undefined) {
    return undefined;
  }

  if (suffix.startsWith('http')) {
    return suffix;
  }

  const baseUrl = bucketUrls[bucket];
  if (baseUrl === undefined) {
    throw new Error(`Unknown bucket ${bucket}`);
  }
  return `${baseUrl}/${suffix}`;
}
