

































































import { computed, defineComponent, PropType } from '@vue/composition-api';
import { ActionLinkTag, ActionLinkData } from '@/app/shared/client/type/action-link';
import { ActionLinkSettings } from '@/app/shared/client/type/settings/action-link-settings';
import ControlLinkIcon from '@/app/website/client/view/icon/ControlLinkIcon.vue';
import { useActionLinkElement } from '@/app/website/client/logic/element/action-link-element';

interface Props {
  content: ActionLinkData,
  design: ActionLinkSettings,
  previewMode: boolean,
  tag: ActionLinkTag,
}

const ActionLink = defineComponent({
  components: {
    ControlLinkIcon,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<ActionLinkData | undefined>,
    },
    design: {
      required: true,
      type: Object as PropType<ActionLinkSettings | undefined>,
    },
    previewMode: {
      required: true,
      type: Boolean,
    },
    tag: {
      required: false,
      type: String as PropType<ActionLinkTag>,
      default: ActionLinkTag.A,
    },
  },
  setup(props: Props, { root }) {
    const content = computed(() => props.content);
    const design = computed(() => props.design);
    return useActionLinkElement(root.$store, content, design);
  },
});

export default ActionLink;
