import { Breakpoints } from '@/app/shared/client/type/settings/viewport-settings';
import { ImageData } from '@/app/shared/client/type/image';
import { WebsiteRuntimeContext } from '@/app/website/client/type/website-runtime-context';
import { Bucket } from '@/app/shared/client/type/bucket';
import getImageUrl from '@/app/shared/client/helper/image-url';
import { ImageInUploadingState } from '@/app/shared/client/type/image-in-uploading-state';

export interface ImageSet {
  urlCropped1000: string | undefined;
  url200: string | undefined;
  url600: string | undefined;
  url1200: string | undefined;
  url2000: string | undefined;
  url3200: string | undefined;
  backgroundColor: string | undefined;
}

export interface ImageCurrentSet {
  thumbnail: string | undefined;
  image1x: string | undefined;
  image2x: string | undefined;
  backgroundColor: string | undefined;
}

export function getImageStyleSelector(
  selector: string,
  imageCurrentSet: ImageCurrentSet,
  breakpoint: Breakpoints | undefined = undefined,
): string {
  const thumbnail = imageCurrentSet.thumbnail ? `, url(${imageCurrentSet.thumbnail})` : '';

  if (breakpoint === undefined) {
    return `${selector} {
      background-color: ${imageCurrentSet.backgroundColor};
      background-image: url(${imageCurrentSet.image1x})${thumbnail};
    }
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: url(${imageCurrentSet.image2x})${thumbnail};
    }`;
  }
  return `@media ${breakpoint} {
    ${selector} {
      background-image: url(${imageCurrentSet.image1x})${thumbnail};
    }
    @media (-webkit-min-device-pixel-ratio: 2) and ${breakpoint},
     (min-resolution: 192dpi) and ${breakpoint} {
      background-image: url(${imageCurrentSet.image2x})${thumbnail};
    }
  }`;
}

function getActualImage(
  uploadingImage: ImageInUploadingState | undefined,
  suffix: string | undefined,
  bucket: Bucket | undefined,
  bucketUrls: Record<Bucket, string | undefined>,
): string | undefined {
  if (uploadingImage !== undefined) {
    return uploadingImage.rawImage;
  }

  return getImageUrl(suffix, bucket, bucketUrls);
}

export function getImageSet(
  context: WebsiteRuntimeContext,
  uploadingImage: ImageInUploadingState | undefined,
  image: ImageData | undefined,
): ImageSet | undefined {
  const set = image?.set;
  const bucket = image?.bucket;
  const bucketUrls = context.bucketUrls ?? {} as Record<Bucket, string | undefined>;
  const imageType = context.webpSupported ? 'webp' : 'default';

  let backgroundColor;
  if (image?.borderInfo !== undefined) {
    const red = image?.borderInfo.color?.r;
    const green = image?.borderInfo.color?.g;
    const blue = image?.borderInfo.color?.b;
    const alpha = image?.borderInfo.color?.a;
    backgroundColor = `rgba(${red}, ${green}, ${blue}, ${alpha})`;
  }

  return {
    urlCropped1000: getActualImage(uploadingImage, set?.[`cropped-${imageType}-1000x2000`]?.url, bucket, bucketUrls),
    url200: getActualImage(uploadingImage, set?.[`${imageType}-200x200`]?.url, bucket, bucketUrls),
    url600: getActualImage(uploadingImage, set?.[`${imageType}-600x600`]?.url, bucket, bucketUrls),
    url1200: getActualImage(uploadingImage, set?.[`${imageType}-1200x1200`]?.url, bucket, bucketUrls),
    url2000: getActualImage(uploadingImage, set?.[`${imageType}-2000x2000`]?.url, bucket, bucketUrls),
    url3200: getActualImage(uploadingImage, set?.[`${imageType}-3200x3200`]?.url, bucket, bucketUrls),
    backgroundColor,
  };
}
